<template>
  <div class="usersearch">
    <div class="tracename">
      <span>{{$t('user.username')}}</span>
    </div>
    <div>
      <el-input v-model="select_user_name" :placeholder="searchholder" clearable></el-input>
    </div>
    <div class="date_input">
      <span class="timespan">{{$t('user.timerange')}}</span>
      <el-date-picker v-model="value1" value-format="yyyy-MM-dd HH:mm:ss" type="daterange" :range-separator="$t('user.to')"
        :start-placeholder="$t('user.startdate')" :end-placeholder="$t('user.enddate')">
      </el-date-picker>
    </div>
    <div>
      <el-button type="primary" @click="search" icon="el-icon-search">{{$t('user.usertrace')}}</el-button>
    </div>
  </div>
</template>
<script>
  export default {
    props: ['searchholder'],
    data() {
      return {
        value1: [],
        select_user_name: '',
      }
    },
    methods: {
      search() {
        this.$emit('getUserActionRecord', this.value1, this.select_user_name)
      }
    }
  }
</script>
<style scoped>
  .usersearch {
    margin: 20px 0;
    height: 40px;
    line-height: 40px;
    display: flex;
    font-size: 14px;
  }

  .tracename {
    margin: 0 20px;
  }

  .date_input {
    height: 46px;
    margin-right: 20px;
  }

  .timespan {
    margin: 0 20px 0 40px;
  }
</style>



