<template>
	<div>
		<v-tracesearch v-on:getUserActionRecord="getUserActionRecord" :searchholder="searchholder"></v-tracesearch>
		<div>
			<template>
				<el-radio-group v-model="rank" @change="TabClick" style="margin-bottom: 4px;" >
			    <el-radio-button label="1">{{$t('user.storage_records')}}</el-radio-button>
			    <el-radio-button label="2">{{$t('user.shelf_records')}}</el-radio-button>
			    <el-radio-button label="3">{{$t('user.user_records')}}</el-radio-button>
				</el-radio-group>
			</template>
			<!-- 仓储记录 -->
			<div v-show="this.rank==1">
				<el-table
					:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
					:data="warehouse_log"
					:empty-text="$t('common.nodata')"
					border>									
					<el-table-column
						prop="in_time"
						:formatter="timeFormatter"
					  :label="$t('user.in_time')">						
					</el-table-column>
					<el-table-column
						prop="shelf_id"
					  :label="$t('materialshelf.shelf_id')">						
					</el-table-column>
					<el-table-column
						prop="position"
					  :label="$t('storage.position')">						
					</el-table-column>
					<el-table-column
						:show-overflow-tooltip='true'
						prop="label_code"
					  :label="$t('storage.label_code')">						
					</el-table-column>
					<el-table-column
						:show-overflow-tooltip='true'
						prop="steel_model"
					  :label="$t('storage.steel_model')">						
					</el-table-column>
					<el-table-column
						:show-overflow-tooltip='true'
						prop="op_code"
						:formatter="storageFormatter"
					  :label="$t('user.op_code')">						
					</el-table-column>
				</el-table>
			</div>
			<!-- 钢网架记录 -->
			<div v-show="this.rank==2">
				<el-table
					:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
					:data="shelf_log"
					border>									
					<el-table-column
						prop="in_time"
						:formatter="timeFormatter"
					  :label="$t('user.in_time')">						
					</el-table-column>
					<el-table-column
						prop="shelf_id"
					  :label="$t('materialshelf.shelf_id')">						
					</el-table-column>
					<el-table-column
						:show-overflow-tooltip='true'
						prop="op_code"
						:formatter="shelfFormatter"
					  :label="$t('user.op_code')">						
					</el-table-column>
				</el-table>
			</div>
			<!-- 用户操作记录 -->
			<div v-show="this.rank==3">
				<el-table
					:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
					:data="user_log"
					border>									
					<el-table-column
						prop="in_time"
						:formatter="timeFormatter"
					  :label="$t('user.in_time')">						
					</el-table-column>
					<el-table-column
						:show-overflow-tooltip='true'
						prop="op_code"
						:formatter="userFormatter"
					  :label="$t('user.op_code')">						
					</el-table-column>
					<el-table-column
						prop="target_name"
					  :label="$t('user.target_name')">						
					</el-table-column>
					<el-table-column
						:show-overflow-tooltip='true'
						prop="target_comment"
					  :label="$t('user.target_comment')">						
					</el-table-column>
				</el-table>
			</div>
			
		</div>
	</div>
</template>
<script>
	import VTracesearch from '@/components/user/commonuser/tracesearch'
	import { userActionRecord } from '@/request/api/user';// 导入api接口
	import { timeConvert } from '@/utils/public'
	export default {
		components:{VTracesearch},
		data () {
			return {
				searchholder:this.$t('user.searchholder'),
				datas:{
					'select_user_name':'',
					'start_time':'',
					'end_time':'',
				},			
				rank:1,
				user_log:[],
				shelf_log:[],
				warehouse_log:[]

			}
		},
		methods:{
			TabClick(value) {
				this.rank = value;
			},
			getUserActionRecord(value1,select_user_name) {
				this.datas.select_user_name = select_user_name
				if (value1) {
					this.datas.start_time = value1[0]
					this.datas.end_time = value1[1]
				}else {
					this.datas.start_time = ''
					this.datas.end_time = ''
				}
				userActionRecord(this.datas).then(res =>{
					if (res.result) {
						this.user_log = res.user_log
						this.shelf_log = res.shelf_log
						this.warehouse_log = res.warehouse_log
					} else {
						this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
					}

				})
			},
			timeFormatter(value){
				return timeConvert(value.in_time,1)
			},
			storageFormatter(value){
				let code = value.op_code
				switch(code) {
					case 1:
						return '条码枪上料'
						break
					case 2:
						return '条码枪取料'
						break
					case 3:
						return '异常取料'
						break
					case 4:
						return '重复上架弹出'
						break
					case 5:
						return '禁用物料'
						break
					case 6:
						return 'API上料'
						break
					case 7:
						return 'API取料'
						break
					case 8:
						return '撤消料架亮灯'
						break
					case 9:
						return '工单取料，首套'
						break
					case 10:
						return '工单取料，全套'
						break
					case 11:
						return '站位顺序发料'
						break
					case 12:
						return '库位整理'
						break
					default:
					return '物料转移上架'
				}
			},
			shelfFormatter(value){
				let code = value.op_code
				switch(code) {
					case 1:
						return '上料超时'
						break
					case 2:
						return '报警'
						break
					case 3:
						return '取消报警'
						break
					case 4:
						return '绑定料架'
						break
					default:
					return '解绑料架'
				}
			},
			userFormatter(value){
				let code = value.op_code
				switch(code) {
					case 1:
						return '添加用户'
						break
					case 2:
						return '删除用户'
						break
					case 3:
						return '修改密码'
						break
					case 4:
						return '修改用户权限组'
						break
					default:
					return '修改用户区域'
				}
			}
		}
	}
</script>


